<template>
  <base-field-label-wrapper
    :text="label"
    :info-text="infoText"
    :label-for="id"
    :is-required="isRequired"
  >
    <template v-if="mask">
      <v-text-field
        :id="id"
        class="body-2 font-weight-semi-bold"
        clear-icon="mdi-close-circle"
        v-mask="mask"
        dense
        outlined
        v-bind="$attrs"
        v-on="$listeners"
      >
        <template v-slot:message="{ message }">
          <base-field-error :message="message" />
        </template>

        <slot
          name="append"
          slot="append"
        ></slot>
        <slot></slot>

        <slot
          name="append-outer"
          slot="append-outer"
        ></slot>
      </v-text-field>
    </template>
    <template v-else>
      <v-text-field
        :id="id"
        class="body-2 font-weight-semi-bold"
        clear-icon="mdi-close-circle"
        dense
        outlined
        v-bind="$attrs"
        v-on="$listeners"
      >
        <template v-slot:message="{ message }">
          <base-field-error :message="message" />
        </template>

        <slot
          name="append"
          slot="append"
        ></slot>
        <slot></slot>

        <slot
          name="append-outer"
          slot="append-outer"
        ></slot>
      </v-text-field>
    </template>
  </base-field-label-wrapper>
</template>

<script>
import { mask } from 'vue-the-mask';
import BaseFieldLabelWrapper from '@components/BaseFieldLabelWrapper';
import BaseFieldError from '@components/BaseFieldError';

export default {
  name: 'BaseFieldInput',
  inheritAttrs: false,
  components: {
    BaseFieldLabelWrapper,
    BaseFieldError,
  },
  directives: {
    mask,
  },
  props: {
    label: {
      type: String,
      default: '',
    },
    infoText: {
      type: String,
      default: '',
    },
    isRequired: {
      type: Boolean,
      default: false,
    },
    mask: {
      type: [String, Array, Boolean],
      default: null,
    },
  },
  computed: {
    id() {
      return this.label + Math.random();
    },
  },
};
</script>
