<template>
  <v-card
    v-if="activeDriver"
    width="100%"
    height="fit-content"
    class="user-card py-lg-5 py-md-12 pt-12 pb-7 mx-lg-2 mx-md-2 mx-5 px-lg-5 px-sm-12"
    :flat="$vuetify.breakpoint.mobile"
  >
    <div class="form-wrapper">
      <v-form
        ref="form"
        v-model="form.valid"
        lazy-validation
        @keyup.enter.native="onSubmit"
      >
        <base-field-input
          v-model="form.values.full_name"
          label="Full Name"
                          :is-required="true"
          :rules="form.fullNameRules"
        />

        <base-field-input
          v-model="form.values.email"
          label="Email"
                          :is-required="true"
          :rules="form.emailRules"
          :error="Boolean((serverErrors && serverErrors.email) || serverError)"
          :error-messages="serverErrors.email"
        />

        <base-field-input
          mask="+1 (###) ###-####"
          v-model="form.values.phone"
          label="Phone"
                          :is-required="true"
          :rules="form.phoneRules"
        />

        <base-field-input
          v-model="form.values.trailer_capacity"
          label="Trailer Capacity"
                          :is-required="true"
          :rules="form.trailerCapacityRules"
        />

        <base-field-input
          v-model="form.values.trailer_length"
          label="Trailer Length"
                          :is-required="true"
          :rules="form.trailerLengthRules"
        />

        <base-field-input
          v-model="form.values.license_number"
          label="Driver’s License Number"
        />

        <base-field-select
          v-model="form.values.issuing_state"
          :items="states.data"
          label="Issuing State/Province"
          item-value="id"
          item-text="name"
          :rules="form.issuingStateRules"
          :error="Boolean((serverErrors && serverErrors.issuing_state) || serverError)"
          :error-messages="serverErrors.issuing_state"
        />

        <v-btn
          :width="$vuetify.breakpoint.mobile ? '100%' : 100"
          :height="$vuetify.breakpoint.mobile ? 50 : 35"
          depressed
          :disabled="!form.valid || !form.edited"
          color="primary"
          class="mt-2"
          :class="{ 'font-weight-bold': $vuetify.breakpoint.mobile }"
          @click="onSubmit"
        >
          Save
        </v-btn>
      </v-form>
    </div>
  </v-card>
</template>

<script>
import BaseFieldSelect from '@components/BaseFieldSelect';
import BaseFieldInput from '@components/BaseFieldInput';

import { required, decimal } from '@utils/helpers/validationRules';
import { createNamespacedHelpers } from 'vuex';
import * as constants from '@modules/drivers/constants';
import * as dictionariesConstants from '@modules/auth/constants';

const { mapGetters, mapActions, mapMutations } = createNamespacedHelpers('drivers');

const {
  mapActions: mapDictionariesActions,
  mapState: mapDictionariesState,
} = createNamespacedHelpers('dictionaries');

export default {
  name: 'DriverEdit',
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.form.edited = !!this.getShallowDifferenceKeys(this.form.values, this.form.initialValues).length;
        this.serverError = '';
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  components: {
    BaseFieldSelect,
    BaseFieldInput,
  },
  data: () => ({
    form: {
      edited: false,
      valid: false,
      initialValues: null,
      values: {},
      fullNameRules: [required('Full Name')],
      emailRules: [required('Email')],
      phoneRules: [required('Email')],
      trailerCapacityRules: [required('Trailer Capacity'), decimal('Trailer Capacity')],
      trailerLengthRules: [required('Trailer Length'), decimal('Trailer Length')],
      issuingStateRules: [decimal('Issuing State')],
    },
    serverErrors: {},
    serverError: '',
  }),
  computed: {
    ...mapGetters({
      activeDriver: constants.getters.ACTIVE_DRIVER,
    }),
    ...mapDictionariesState(['states']),
  },
  methods: {
    onSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) {
        this.$notificationError('Form not valid');
        return;
      }

      const data = {};
      this.getShallowDifferenceKeys(this.form.values, this.form.initialValues).forEach((editedKey) => {
        data[editedKey] = this.form.values[editedKey];
      });

      this.editDriver({ id: this.activeDriver.id, data })
        .then(() => {
          this.form.edited = false;
          this.form.initialValues = { ...this.form.values };
          this.getDrivers();
          this.$router.push({ name: this.$constants.ROUTE_NAMES.DRIVERS });
          this.$notificationSuccess('Driver was updated');
        })
        .catch((errors) => {
          if (Array.isArray(errors)) {
            this.serverError = errors;
          } else {
            this.serverErrors = errors;
          }
          this.$notificationError('Driver\'s information wasn\'t updated');
        });
    },
    getShallowDifferenceKeys(obj1, obj2) {
      return Object.keys(obj1)
        .filter((key) => obj2[key] !== obj1[key]);
    },
    ...mapActions({
      editDriver: constants.actions.EDIT_DRIVER,
      getDrivers: constants.actions.GET_DRIVERS,
    }),
    ...mapMutations({
      updateActiveDriverId: constants.mutations.UPDATE_ACTIVE_DRIVER_ID,
    }),
    ...mapDictionariesActions({
      getStates: dictionariesConstants.actions.GET_STATES,
    }),
  },
  mounted() {
    this.updateActiveDriverId(this.$route.params.id);
    this.getStates();
    if (this.activeDriver) {
      this.form.values = {
        full_name: this.activeDriver.full_name && this.activeDriver.full_name,
        email: this.activeDriver.email && this.activeDriver.email,
        phone: this.activeDriver.phone && this.activeDriver.phone.startsWith('+1') ? this.activeDriver.phone.slice(2) : this.activeDriver.phone,
        trailer_capacity: this.activeDriver.trailer_capacity && String(this.activeDriver.trailer_capacity),
        trailer_length: this.activeDriver.trailer_length && String(this.activeDriver.trailer_length),
        license_number: this.activeDriver.license_number && String(this.activeDriver.license_number),
        issuing_state: this.activeDriver.issuing_state && this.activeDriver.issuing_state,
      };
      this.form.initialValues = { ...this.form.values };
    }
  },
};
</script>
